import { saveAs } from './external/file-saver.min.js';
export { saveAs };
export const getUnique = (arr) => [...new Set(arr)];
export const stringPad = {
    pad: (val, len) => {
        val = String(val);
        len = len || 2;
        while (val.length < len)
            val = '0' + val;
        return val;
    },
    padEmpty: (num, size) => {
        const s = '   ' + num;
        return s.substr(s.length - size);
    },
    pad0: (str, max) => (str.length < max ? stringPad.pad0('0' + str, max) : str),
};
export const saveVariable = (variable, filename) => {
    try {
        filename = typeof filename == 'undefined' ? 'variable.txt' : filename;
        variable = JSON.stringify(variable);
        const blob = new Blob([variable], { type: 'text/plain;charset=utf-8' });
        if (!saveAs)
            throw new Error('saveAs is unavailable!');
        saveAs(blob, filename);
    }
    catch (e) {
        // console.debug('Unable to Save File!');
    }
};
export const saveCsv = (items, name) => {
    try {
        const replacer = (value) => (value === null ? '' : value); // specify how you want to handle null values here
        const header = Object.keys(items[0]);
        let csv = items.map((row) => header.map((fieldName) => JSON.stringify(replacer(row[fieldName]))).join(','));
        csv.unshift(header.join(','));
        csv = csv.join('\r\n');
        const blob = new Blob([csv], { type: 'text/plain;charset=utf-8' });
        if (!saveAs)
            throw new Error('saveAs is unavailable!');
        saveAs(blob, `${name}.csv`);
    }
    catch (error) {
        // console.debug('Unable to Save File!');
    }
};
export const parseRgba = (str) => {
    // eslint-disable-next-line no-useless-escape
    let [r, g, b, a] = str.match(/[\d\.]+/gu);
    r = (parseInt(r) / 255);
    g = parseInt(g) / 255;
    b = parseInt(b) / 255;
    a = parseFloat(a);
    if (isNaN(r) || isNaN(g) || isNaN(b) || isNaN(a)) {
        // console.warn('Bad RGBA! Using White Instead.');
        return [1, 1, 1, 1];
    }
    else {
        return [r, g, b, a];
    }
};
export const hex2RgbA = (hex) => {
    // eslint-disable-next-line prefer-named-capture-group
    if (/^#([A-Fa-f0-9]{3}){1,2}$/u.test(hex)) {
        let c = hex.substring(1).split('');
        if (c.length == 3) {
            c = [c[0], c[0], c[1], c[1], c[2], c[2]];
        }
        c = '0x' + c.join('');
        const r = ((parseInt(c) >> 16) & 255) / 255;
        const g = ((parseInt(c) >> 8) & 255) / 255;
        const b = (parseInt(c) & 255) / 255;
        return [r, g, b, 1];
    }
    // console.warn('Bad Hex! Using White Instead.');
    return [1, 1, 1, 1];
};
export const rgbCss = (values) => `rgba(${values[0] * 255},${values[1] * 255},${values[2] * 255},${values[3]})`;
/**
 *
 * @param {string} str Input string
 * @param {number} num Maximum length of the string
 * @returns {string} Trunicated string
 */
export const truncateString = (str, num) => {
    if (typeof str == 'undefined')
        return 'Unknown';
    // If the length of str is less than or equal to num
    // just return str--don't truncate it.
    if (str.length <= num) {
        return str;
    }
    // Return str truncated with '...' concatenated to the end of str.
    return str.slice(0, num) + '...';
};
window.getUnique = getUnique;
window.saveCsv = saveCsv;
